.home-page {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  .audios {
    align-self: center;
    .audio {
      margin-top: 20px;
      height: 40px;
      width: 350px;
      border-radius: 7px;
      display: flex;
      align-items: center;
    }
  }
  .controle {
    align-self: center;
    margin-top: 20px;
    width: 50%;
    display: flex;
    justify-content: space-around;
    button {
      height: 35px;
      width: 100px;
      border-radius: 4px;
    }
  }
}

.slide-in-br {
  -webkit-animation: slide-in-br 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-in-br 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

